body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  min-height: 100vh;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Custon style start */
span {
  font-family: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono",
    "Lucida Console", Monaco, monospace;
}
.btn {
  background-color: transparent;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

.body-wrapper {
  width: 100%;
  min-height: 100%;
  overflow-x: hidden;
  -webkit-font-smoothing: antialiased;
  background-color: rgb(10, 25, 47);
  color: rgb(136, 146, 176);
  line-height: 1.3;
  font-family: Calibre, "San Francisco", "SF Pro Text", -apple-system, system-ui,
    BlinkMacSystemFont, Roboto, "Helvetica Neue", "Segoe UI", Arial, sans-serif;
  font-size: 20px;
  margin: 0px;
}

.banner-content {
  position: absolute;
  top: 50%;
  max-width: 800px;
  margin: 0 auto;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
}
.banner-content .mini-head {
  color: rgb(100, 255, 218);
  font-size: 16px;
  font-family: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono",
    "Lucida Console", Monaco, monospace;
}

.banner-content .name-header {
  font-size: 80px;
  line-height: 1.1;
  margin: 0px;
  color: #ffffff;
}

.banner-content .desc {
  font-size: 68px;
  line-height: 1.1;
  color: rgb(136, 146, 176);
}

.banner-text {
  max-width: 403px;
  padding: 20px 0;
}

.banner-text ~ .btn {
  border: 1px solid #64ffda;
  color: #64ffda;
  padding: 10px 25px;
}

section.banner-section {
  position: relative;
  height: 100vh;
  width: 100%;
}
.specific-spacing {
  padding: 120px 0;
}

.intro-head span {
  padding-right: 20px;
  font-size: 19px;
  font-weight: 600;
  color: #64ffda;
}

.intro-head h3:after {
  content: "";
  position: absolute;
  height: 1px;
  background-color: #ffffff;
  right: 0;
  left: 179px;
  width: 300px;
  top: 23px;
}

.intro-head h3 {
  display: inline-block;
  color: #ffffff;
  font-size: 30px;
  position: relative;
}

.intro-head {
  padding-bottom: 30px;
}

.about-block p {
  font-size: 17px;
  line-height: 27px;
}

.profile-image {
  height: 350px;
  width: 350px;
  border: 2px solid #ffffff;
  margin: 0 auto;
}

.profile-image {
  background-image: url(/portfolio/static/media/banner-profile.1d0c81a1.jpg);
  background-size: 349px;
  background-repeat: no-repeat;
}

.techonology-block {
  max-width: 500px;
}

.something-built-by-me h3:after {
  left: 315px !important;
}

.techonology-block ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

.techonology-block ul li {
  color: #ffffff;
  width: 50%;
  float: left;
  padding-bottom: 15px;
  font-size: 16px;
  letter-spacing: 0.1em;
  position: relative;
  padding-left: 17px;
}

.techonology-block p {
  margin-bottom: 50px;
}

.techonology-block ul li:before {
  content: "\25B9";
  position: absolute;
  left: 0px;
  color: rgb(100, 255, 218);
  font-size: 14px;
  line-height: 12px;
  top: 6px;
}
.where-working-heading h3:after {
  left: 296px;
}
.current-working h3 {
  font-size: 22px;
  color: #ffffff;
}

.current-working h4 {
  font-size: 16px;
  color: #27dbda;
}

.roles-responsibilites {
  padding-top: 35px;
}

.roles-responsibilites ul > li {
  line-height: 40px;
  position: relative;
  color: #ffffff;
  font-size: 17px;
  padding-left: 21px;
}

.roles-responsibilites ul > li:before {
  content: "\25B9";
  position: absolute;
  left: 0px;
  color: rgb(100, 255, 218);
  font-size: 14px;
  line-height: 12px;
  top: 16px;
}

.profile-image:after {
  content: "";
  position: absolute;
  border: 2px solid #ffffff;
  height: 357px;
  width: 358px;
  right: -9px;
  top: -21px;
  z-index: 5;
}

a.projects-link {
  min-height: 85px;
  display: -webkit-flex;
  display: flex;
  -webkit-align-items: center;
          align-items: center;
  border: 2px solid #ffffff;
  text-align: center;
  -webkit-justify-content: center;
          justify-content: center;
  margin-bottom: 30px;
  border-radius: 10px;
}

.projects-block {
  margin-top: 30px;
}

a.projects-link > span {
  display: block;
  color: #ffffff;
  text-transform: uppercase;
  letter-spacing: 0.1em;
}

.text-center.project-inner .more-projects {
  border-color: #64ffda;
  color: #64ffda;
  padding: 9px 23px;
}
.hideblock {
  display: none;
}
.fewprojects ul > li > a {
  color: #ffffff;
  font-size: 18px;
  display: inline-block;
}

.fewprojects ul > li {
  display: inline-block;
  width: 16%;
  padding: 10px;
  border: 1px solid #ffffff;
  margin: 10px;
  border-radius: 10px;
}

.fewprojects.showblock {
  text-align: center;
  margin-top: 30px;
}

.intro-head.where-working-heading h5 {
  text-align: center;
  display: inline-block;
  color: #ffffff;
}

.getintouch {
  text-align: center;
}

.getintouch h4 {
  font-size: 70px;
  color: #ffffff;
  padding-bottom: 30px;
}

.getintouch p {
  max-width: 500px;
  margin: 0 auto;
  color: #ffffff;
  line-height: 24px;
  font-size: 15px;
}

.contact-info label {
  display: inline-block;
  padding-right: 21px;
  color: #64ffda;
}

.contact-info span {
  color: #ffffff;
}

.contact-info {
  padding-top: 30px;
}
/* Responsive CSS */

@media only screen and (max-width: 991px) {
  .profile-image:after {
    content: none;
  }
  .profile-image {
    margin-bottom: 30px;
  }
}

@media only screen and (max-width: 767px) {
  .specific-spacing {
    padding: 70px 0;
  }
  .banner-content .name-header {
    font-size: 48px;
  }
  .banner-content .desc {
    font-size: 33px;
  }
  .intro-head h3:after {
    content: none;
  }
  .intro-head h3 {
    font-size: 18px;
  }
  .profile-image{
    width: 280px;   
    height: 280px;
    background-size: 100%;
  }
  .getintouch h4 {
    font-size: 35px;
  }
  .current-working h3 {
    font-size: 15px;
  }
  .roles-responsibilites ul > li {
    font-size: 15px;
  }
  .fewprojects ul > li {
    width: 40%;
  }
  .roles-responsibilites ul>li{
    padding-bottom: 10px;
    line-height: 24px;
  }
  .banner-text > p {
    font-size: 15px;
    line-height: 25px;
}
}

